.List {
  display: flex;
  margin: 40px auto;
  flex-direction: column;
  align-items: center;
  border-radius: 8px;
}

.Item {
  font-size: 12px;
  display: flex;
  align-items: center;
  width: 350px;
  height: 100px;
  border-radius: 4px;
  box-shadow: 0px 4px 4px hsla(0, 0%, 0%, 0.15);
  padding: 20px;
  margin-bottom: 10px;
  font-size: 24px;
  font-weight: 500;
  background-color: white;
}

.Item:hover {
  cursor: pointer;
  box-shadow: 2px 7px 7px hsla(0, 0%, 0%, 0.15);
}

.Avatar {
  margin-left: 20px;
  margin-right: 20px;
}

.Status {
  display: inline-flex;
  width: 15px;
  height: 15px;
  border-radius: 50%;
  background-color: green;
}

.Online {
  composes: Status;
  background-color: green;
}
.Offline {
  composes: Status;
  background-color: red;
}
