.Statistics {
  width: 400px;
  box-shadow: 0px 4px 4px hsla(0, 0%, 0%, 0.15);
  margin: 0 auto;
  border: 1px solid rgb(253, 252, 252);
  border-radius: 8px;
  background-color: white;
}

.Statictics:hover {
  box-shadow: 2px 7px 7px hsla(0, 0%, 0%, 0.15);
}

.Title {
  text-align: center;
}

.List {
  display: flex;
  flex-direction: row;
}

.Item {
  display: flex;
  flex-direction: column;
  flex-basis: calc(100% / 5 - 15px);
  text-align: center;
  color: beige;
  background-color: aqua;
}
.Item:not(:last-child) {
  margin-right: 10px;
}
